<template>
	<div class="wrap_login">
		<div class="login_div">
			<h3 class="title_logo">
				<img src="../../../assets/images/swissmic-logo-black.png" alt="" >
				<span class="title_span">wms</span>	
			</h3>
			<span class="zh_en">
				<el-dropdown>
				  	<span class="el-dropdown-link">
				   		<img src="../../../assets/images/zhen.png" alt="">
				  	</span>
				  	<el-dropdown-menu slot="dropdown" hide-on-click="true">
				    	<el-dropdown-item><span class="span_lang" @click="changeChina">{{$t("common.zh")}}</span></el-dropdown-item>
				    	<el-dropdown-item><span class="span_lang" @click="changeEnglish">{{$t("common.en")}}</span></el-dropdown-item>
				    </el-dropdown-menu>
				</el-dropdown>				
			</span>
			<el-form :model="userForm" ref="userForm">
			  <el-form-item prop="username">
			    <input v-model="userForm.username"  :placeholder="$t('login.Username')" class="user_input el-input__inner"></input>
			  </el-form-item>
			  <el-form-item prop="password">
			    <input type="password" v-model="userForm.password"  :placeholder="$t('login.Password')" class="password_input el-input__inner"></input>
			  </el-form-item>
			  <el-button class="login_btn el-button" @click="login">{{$t("login.Login")}}</el-button>
			  <div class="login_tips" v-if="passwordErr">{{$t('login.PasswordOrUserIsInvalid')}}</div>
			  <div class="login_tips" v-if="otherErr">{{errTips}}</div>
			</el-form>
		</div>
	</div>
</template>
<script>
// import { mapMutations } from 'vuex'
// import { loginApi } from '../../request/api/login';// 导入我们的api接口
import QS from 'qs'; 
export default{
	data()	{
		return{
			userForm:{
				username:'',
				password:''
			},
			passwordErr:false,
			otherErr:false,
			errTips:'',
		}
	},
	mounted() {

	},
	methods:{
		// ...mapMutations(['changeLogin']),
		changeChina(){
			this.$i18n.locale = 'zh';
			localStorage.setItem('lang', 'zh')
		},
		changeEnglish(){
			this.$i18n.locale = 'en';
			localStorage.setItem('lang', 'en')
		},
		login(){
			let _this = this;
			this.$http({
				url: '/user/login',
				method: 'post',
				data: {
				  	user_name: _this.userForm.username,
				  	user_passwd: _this.userForm.password
				},
				headers:{'Content-Type':'application/json'}
			}).then(res=>{
				if (res.status ==200) {
					if (res.data.result) {
						let data = res.data;
						localStorage.setItem('user_name',data.user_data.user_name);
						localStorage.setItem('user_id',data.user_data.user_id);
						localStorage.setItem('user_group_power',data.user_data.user_group_power);
						// _this.changeLogin({ user: data.user_data.user_name});		
						_this.$router.push('/pdasearch')		
					}else{
						_this.otherErr = true;
						_this.errTips = res.data.msg;
						setTimeout(function(){
							_this.otherErr = false;
						},3000)
					}
				}else{

				}							
			})
		}
	}
}
</script>
<style scoped>
body{
	margin:0;
	padding:0;
}
	.wrap_login{
		position: fixed;
		width:100%;
		height: 100%;
		text-align: center;
	}
	.zh_en{
		position: absolute;
		top: 0.46rem;
		right: 0.2rem;
	}
	.zh_en img{
		width:0.75rem;
		height: 0.75rem;
	}
	.login_div{
		width:9.2rem;
		height: 7.52rem;
		position: relative;
		top: 50%;
		left: 50%;
		margin:-3.76rem 0 0 -4.6rem;
	}
	.login_div .el-input__inner{
		width:9.2rem;
		height: 1rem;
		font-size: 0.32rem;
	}
	.login_div .el-input{
		width:9.2rem;
		height: 1rem;
	}
	.login_div input{
		border: 0.05rem solid #dadfe6;
		border-radius: 0.05rem;
	}
	.user_input:focus {
	    outline: none;
	    border: 0.05rem solid #dadfe6;
	}
	.password_input:focus{
		 outline: none;
	    border: 0.05rem solid #dadfe6;
	}
	.title_logo{
		margin-bottom: 0.8rem;
	}
	.title_logo img{
		width:3.63rem;
		height: 1.06rem;
		text-align: center;
	}
	.title_span{
		height: 1.07rem;
		line-height: 1.07rem;
		font-size: 0.91rem;
		color: #181818;
		padding-left: 0.27rem;
		font-weight: 500;
	}
	.span_lang {
		display: inline-block;
		color: #666666!important;
		height: 0.8rem;
		width:1.6rem;
		line-height: 0.8rem;
	}
	.span_lang:hover{
	color: #74d6e7!important;
	}
	.login_btn{
		width:9.2rem;
		height: 1.07rem;
		background: #74d6e7;
		color: #ffffff;
		font-size: 0.38rem;
	}
	.login_tips{
		height: 0.8rem;
		line-height: 0.8rem;
		color: red;
		background: #f1f5f8;
		margin-top: 0.27rem;
		border-radius: 0.05rem;
	}
	.login_div .el-button:active{
		background: #5dcfe3;
		box-shadow: 0 0 0 0 #50b2c3, 0 0 20px 0 #5dcfe3;
	}
</style>


